import axios from '@axios'

export default [
  {
    path: '/prodac',
    name: 'ocade-prodac',
    component: () => import('@/views/ocade/talleres-globales/TalleresGlobalesListar.vue'),
    meta: {
      codigo: 'OCADE-ALT-COMP',
      pageTitle: 'PRODAC',
      breadcrumb: [
        {
          text: 'Programa Deportivo de Alta Competencia',
          active: true,
        },
      ],
    },
    props: {
      codigo: 'OCADE-ALT-COMP',
    },
  },
  {
    path: '/prodac/global/:id/talleres',
    name: 'ocade-prodac-talleres',
    component: () => import('@/views/ocade/talleres-globales/talleres/TalleresListar.vue'),
    beforeEnter: (to, from, next) => {
      axios.get(`/servicio-cultural-deportivo/OCADE-ALT-COMP/global/${to.params.id}`)
        .then(() => next())
        .catch(() => {
          next({ name: 'ocade-prodac-talleres' })
        })
    },
    props: {
      codigo: 'OCADE-ALT-COMP',
    },
  },
]
